
import React from 'react';

// eslint-disable-next-line
import Collapsible from 'react-collapsible';

// eslint-disable-next-line
import {ChangeMapLink, ChangeTopicLink} from './utils';

function Layer2(props) {

    return(
    <div className="text-holder"> <h1 id="layer-1-motivation-for-the-slhp">Layer 1: Motivation for the SLHP</h1> <Collapsible trigger="Introduction" overflowWhenOpen="visible"> <p>During the 1920s and 1930s, the University of North Carolina at Chapel Hill became an epicenter of debates about how to create sociological knowledge by identifying and addressing the problems of the South.<sup className="tooltip"><span>Guy B Johnson and Guion Griffis Johnson, <em>Research in Service to Society: The First Fifty Years of the Institute for Research in Social Science at the University of North Carolina</em> (UNC Press Books, 2018); Michel J Lacey and Mary O Furner, <em>The State and Social Investigation in Britain and the United States</em> (Cambridge University Press, 1993); Alice O’Connor, <em>Poverty Knowledge: Social Science, Social Policy, and the Poor in Twentieth-Century US History</em>, vol. 59 (Princeton University Press, 2009); Dorothy Ross, <em>The Origins of American Social Science</em>, vol. 19 (Cambridge University Press, 1992); Mark C Smith, <em>Social Science in the Crucible: The American Debate over Objectivity and Purpose, 1918-1941</em> (Duke University Press, 1994).</span><div>[1]</div></sup> As the institution of higher education aspired to become a national research university and an intellectual leader in the region, the opportunity to address the region’s cultural, economic, and social conditions was led by two increasingly prestigious institutional units: University of North Carolina Press (UNC Press) and the Institute for Research in Social Sciences (IRSS). William "Bill" T. Couch, who took the reins of UNC Press in 1925 and became director in 1932, published work that did not shy away from the South’s problems, demonstrating that reflexive, critical scholarship could come from within the region. Regularly publishing work by acclaimed sociologist Howard Odum and the IRSS, Couch began to question if academic prose driven by statistics adequately communicated the challenges of the region to scholarly and popular audiences. He worried that it often failed to represent the actual lives of the people being documented accurately.</p> <p>The stakes of the debate heightened with the Great Depression, which placed the American South under a microscope. The region’s economic precarity combined with a culture of segregation further cemented the region’s reputation as anti-modern, backward, and impervious to progress; questions about the region’s fitness for full inclusion and citizenship abounded.<sup className="tooltip"><span>Edward L Ayers, <em>The Promise of the New South: Life after Reconstruction</em> (Oxford University Press, 2007); Grace Elizabeth Hale, <em>Making Whiteness: The Culture of Segregation in the South, 1890-1940</em> (Vintage, 2010); Glenda Elizabeth Gilmore, <em>Gender and Jim Crow: Women and the Politics of White Supremacy in North Carolina, 1896-1920</em> (Chapel Hill: UNC Press, 1996); Bruce J Schulman, <em>From Cotton Belt to Sunbelt: Federal Policy, Economic Development, and the Transformation of the South, 1938-1980</em> (Duke University Press, 1994); Fred C Smith, <em>Trouble in Goshen: Plain Folk, Roosevelt, Jesus, and Marx in the Great Depression South</em> (Univ. Press of Mississippi, 2014); The National Emergency Council, "Report on Economic Conditions of the South" (Washington, DC, July 1938), Internet Archive, <a target="_blank" rel="noopener noreferrer" href="https://archive.org/details/reportoneconomic00nati">[link]</a>.</span><div>[2]</div></sup> How to assess and represent the challenges of the region became a central debate.<sup className="tooltip"><span>For more on the history of the Great Depression by state, see: Douglas Carl Abrams, <em>Conservative Constraints: North Carolina and the New Deal</em> (University Press of Mississippi, 1992); Roger Biles, <em>The South and the New Deal</em> (University Press of Kentucky, 2006); George Blakey, <em>Hard Times and the New Deal in Kentucky, 1929–1939</em> (Lexington: University Press of Kentucky, 1986); Jack Irby Hayes, <em>South Carolina and the New Deal</em> (Univ of South Carolina Press, 2001); Ronald L Heinemann, <em>Depression and New Deal in Virginia: The Enduring Dominion</em> (University of Virginia Press, 1983); Douglas Smith, <em>The New Deal in the Urban South</em> (Baton Rouge: Louisiana State University Press, 1988); Jerry Bruce Thomas, <em>An Appalachian New Deal: West Virginia in the Great Depression</em> (University Press of Kentucky, 1998).</span><div>[3]</div></sup> Two warring schools of intellectual thought framed the academic and literary representations of the South.<sup className="tooltip"><span>Christopher M Duncan, <em>Fugitive Theory: Political Theory, the Southern Agrarians, and America</em> (Lexington Books, 2000); Bob Holladay, "The Gods That Failed: Agrarianism, Regionalism, and the Nashville-Chapel Hill Highway," <em>Tennessee Historical Quarterly</em> 64, no. 4 (2005): 284–307; Twelve Southerners, <em>I’ll Take My Stand: The South and the Agrarian Tradition</em> (Louisiana State University Press, 1930).</span><div>[4]</div></sup> The Agrarians, based at Vanderbilt University, romanticized a return to White, rural, folk culture arguing sociological scholarship was the handmaiden of Northern intellectuals bent on the erosion of Southern traditional values. Howard Odum and his UNC-Chapel Hill colleagues, on the other hand, advocated for systematic, scientific studies of the region through fieldwork conducted by experts so that solutions to social problems could be identified. The evidence constructed a region that was distinct due to its regional culture but also a part of modernity and therefore the nation, earning them a reputation as advocates for "the New South."</p> <p>Couch and UNC Press offered another angle on the debate. Attuned to the literary marketplace, Couch understood that readers – who were primarily White, urban, affluent, and held significant social and political power in US society - were eager to learn about the region. UNC Press, he argued, should be at the center of releasing cutting-edge scholarship about the South, for the Press and its authors were best positioned to produce academically rigorous intellectual work. They should not be "inoffensive" books or hyperbolic literature but ask difficult and challenging questions, he contended.<sup className="tooltip"><span>Daniel Joseph Singal, <em>The War Within: From Victorian to Modernist Thought in the South, 1919-1945</em> (UNC Press Books, 1982), 273.</span><div>[5]</div></sup> Rather, they should be books that offered a lens into the region, often aided by a sociological bent.</p> <p>While Couch appreciated scholarship that offered sociological knowledge, especially those concerning the Southern region written for a broader public, he questioned the field’s quantitative turn in the early 20<sup>th</sup> century. He did not believe that faceless, generalized statistics in dense academic prose effectively communicated the conditions in the region. An ardent believer that the South had much to offer the nation, which would only improve if the region addressed their serious issues, he set out on a mission to find more ways to document and understand the region. The New Deal would open up an exciting opportunity to put ideas into action. This layer explores the larger historical context and academic debates in Chapel Hill that led to Couch’s idea of the Southern Life History Project.</p> </Collapsible> <Collapsible trigger="Welcome to Chapel Hill" overflowWhenOpen="visible"> <p>Paving Franklin Street was just one sign of a town on the rise in the 1920s. Chapel Hill was growing as the state’s flagship University expanded and approved over a million dollars in construction projects.<sup className="tooltip"><span>Ryan Davis, "Chapel Hill in the Depression," <em>The Daily Tar Heel</em>, March 16, 2009, <a target="_blank" rel="noopener noreferrer" href="https://www.dailytarheel.com/article/2009/03/chapel-hill-in-the-depressionbr">[link]</a>.</span><div>[6]</div></sup> Half a million was designated for Graham Memorial Hall, a student union intended to serve as the center of student life. Named after the President of UNC during World War I, the building’s name and prominence signaled the university’s aspirations. Edward Kidder Graham had sought to transform UNC into a research university that, as he stated, "would emphasize the fact that research and classical culture rightly interpreted are as deeply and completely service as any vocational service."<sup className="tooltip"><span>"Graham Memorial," Names in Brick and Stone: Histories from UNC’s Built Landscape, accessed September 23, 2019, <a target="_blank" rel="noopener noreferrer" href="http://unchistory.web.unc.edu/building-narratives/graham-memorial/#_ftnref1">[link]</a>.</span><div>[7]</div></sup> His emphasis on the pursuit of study and research in the liberal arts was shaped by contemporary debates about the goals of the modern university. UNC, he argued, should become a preeminent research university committed to molding students with a concern for the public good.<sup className="tooltip"><span>Helen Oldham Dennis, "Graham, Edward Kidder," NCPEDIA, accessed September 23, 2019, <a target="_blank" rel="noopener noreferrer" href="https://www.ncpedia.org/biography/graham-edward-kidder">[link]</a>.</span><div>[8]</div></sup> To realize these goals meant building the necessary infrastructure, and UNC had plans to expand southward from Franklin Street rapidly.</p> <figure><img src='img/figure1-1.jpg' alt='figure' onClick={() => props.handleImage(1)}/><figcaption><b>Figure 1.</b> University of North Carolina boys in their car in front of the post office in Chapel Hill, North Carolina, 1940. Wolcott, Marion Post. 1940. University of North Carolina boys in their car in front of the post office. Chapel Hill, North Carolina. Negative. Farm Security Administration - Office of War Information Photograph Collection (Library of Congress). </figcaption></figure> <p>While the freshly paved street on which a generation new to car ownership drove Model T’s was a Southern booster’s dream, the main thoroughfare offered daily reminders of the social order.<sup className="tooltip"><span>Reiko Hillyer, <em>Designing Dixie: Tourism, Memory, and Urban Space in the New South</em> (University of Virginia Press, 2015), <a target="_blank" rel="noopener noreferrer" href="https://www.jstor.org/stable/j.ctt9qhb4m.2">[link]</a>.</span><div>[9]</div></sup> UNC and Chapel Hill leaders’ aspirations were shaped by racialized and gendered understandings of who constituted the public. Segregation defined Southern life, and Chapel Hill was no exception. While African Americans had built and maintained a great deal of the campus since its creation in 1789 and were continually employed in domestic and labor-intensive work such as cooking and cleaning, they were denied entry into the classroom. In fact, the state would go as far as to offer scholarships for young Black men to attend institutions like the University of Michigan rather than desegregate.<sup className="tooltip"><span>James Atwater, Interview with James Atwater, February 28, 2001., February 28, 2001, Interview K-0201, Southern Oral History Program Collection (#4007), <a target="_blank" rel="noopener noreferrer" href="https://docsouth.unc.edu/sohp/playback.html?base_file=K-0201&amp;duration=01:18:24">[link]</a>; Rebecca Clark, Interview with Rebecca Clark, June 21, 2000., June 21, 2000, Interview K-0536, Southern Oral History Program Collection (#4007), <a target="_blank" rel="noopener noreferrer" href="https://docsouth.unc.edu/sohp/playback.html?base_file=K-0536&amp;duration=03:18:40">[link]</a>.</span><div>[10]</div></sup> Chapel Hill’s and the state’s flagship university’s aspirations and challenges were indicative of the era.<sup className="tooltip"><span>White women were allowed to enroll in the university as transfer students beginning in the early 1900s but could not enter as freshmen until 1940. The first Black woman (Karen Lynn Parker) attended UNC in 1963. Dean, Pamela. <em>Women on the Hill: A History of Women at UNC</em>. The Library of the University of North Carolina at Chapel Hill, 1987. "Karen L. Parker Diary, Letter, and Clippings, 1963-1966." Collection no. 05275-z, Southern Historical Collection, The Wilson Library, The University of North Carolina at Chapel Hill, <a target="_blank" rel="noopener noreferrer" href="https://finding-aids.lib.unc.edu/05275/">[link]</a>.</span><div>[11]</div></sup></p> </Collapsible> <Collapsible trigger="The Southern Research University" overflowWhenOpen="visible"> <p>The 1920s heralded a cultural shift made possible by post-war economic prosperity, an expanding consumer culture, and increasing progressive social mores.<sup className="tooltip"><span>Lizabeth Cohen, <em>A Consumers’ Republic: The Politics of Mass Consumption in Postwar America</em>, 1. Vintage Books ed (New York, NY: Vintage Books, 2004); Hale, <em>Making Whiteness: The Culture of Segregation in the South, 1890-1940</em>.</span><div>[12]</div></sup> The accouterments of modernity such as cinemas and radios multiplied in urban spaces as millions of Americans moved from the countryside. New South boosters were also eager to advertise the region’s embrace of certain trappings of modernity, including participation in commercial markets in rural areas, small towns, and cities alike, a process that was well underway during the late 1800s.<sup className="tooltip"><span>Edward L Ayers, <em>The Promise of the New South: Life after Reconstruction</em> (Oxford University Press, 2007); C Van Woodward, <em>Origins of the New South: 1877-1915</em> (Baton Rouge: Louisiana State University Press, 1951); Alex Lichtenstein, <em>Twice the Work of Free Labor: The Political Economy of Convict Labor in the New South</em> (New York: Verso, 1996).</span><div>[13]</div></sup> Trains connected small towns across the South to a national and global economy, while the introduction of cars changed Southerners’ relationship to mobility. While eager to advertise certain kinds of modernity, White power brokers were less eager to advertise the Jim Crow laws designed to shore up segregation and maintain white supremacy, which became a distinguishing feature that earned the South a reputation for being exceptional. The calls for modernization were also echoed inside of Southern universities where intellectual elites argued that well-respected institutions of higher education were a sign of progress.<sup className="tooltip"><span>For example, Tulane University and the University of Virginia articulated such aspirations under the leadership of Edwin Alderman. The connections between UNC and UVa continue. Born in North Carolina and a graduate of UNC, Alderman, who served as President of Tulane and then UVa, spoke at the UNC Presidential Inauguration of Graham. UVa would also receive support from the Rockefeller Foundation to create a social science institute, which would result in the largest area of expansion in faculty and also be called the IRSS. For more info, see Dumas Malone, <em>Edwin A. Alderman</em>, (New York: Doubleday, Doran &amp; Company, Inc, 1940).</span><div>[14]</div></sup> Efforts throughout the 1920s to raise the profile of UNC as a research university were part and parcel of modernization. Administrators and researchers shared the belief that those who actually resided in the region should have a say about its conditions.</p> <figure><img src='img/figure1-2.jpg' alt='figure' onClick={() => props.handleImage(2)}/><figcaption><b>Figure 2.</b> University of North Carolina - Chapel Hill’s Wilson Library, which opened in 1929. The photo was taken in 1939 by Marion Post Wolcott on assignment for another New Deal unit, the Farm Security Administration Historic Division. Wolcott, Marion Post. 1939. Part of the campus. University of North Carolina, Chapel Hill. Orange County, North Carolina. Negative. Farm Security Administration - Office of War Information Photograph Collection (Library of Congress), https://www.loc.gov/pictures/item/2017801953/. </figcaption></figure> <p>UNC Press was the first university press established in the South, and just three years after its creation, Bill Couch took its reins. Over his next 20 years at the Press, Couch transformed the publisher into one of the leading university presses in the nation, helping to fulfill the UNC administrators’ goal of establishing the state flagship as one of the top research universities in the South. Couch fulfilled the administration’s goal of scholarly research by dedicating the Press’ focus on the social, economic, and intellectual well-being of the Southeast region. The way the Press used its pages to publish research on the South reveals conflicts over who could publish, what counted as scholarly knowledge, and for whom to publish at UNC and throughout the region during the tumultuous times of the Great Depression and the reign of Jim Crow. In many ways, these conflicts of thought also played out in Couch’s own life.</p> <p>Couch spent the first seventeen years in rural Virginia, where his father earned a living as a local Baptist preacher. Seeking financial stability, the family moved to Chapel Hill in 1917 when his father decided to turn in the collar for the plow. Young Couch worked on the family farm, which floundered, and then was briefly employed by the Southern Power Company before matriculating at UNC-Chapel Hill in 1920. However, his tenure was brief, and he joined the Army as World War I waged. He returned to UNC-Chapel Hill two and a half years later and became involved in the student publication <em>Carolina Magazine</em>. Couch was still an undergraduate when he caught the attention of University librarian and UNC Press director Louis Round Wilson, thanks to his work as editor of <em>Carolina Magazine</em>. Recognizing an opportunity to shape intellectual thought, Couch became Acting Director when Wilson became suddenly ill.<sup className="tooltip"><span>Daniel Joseph Singal, <em>The War Within: From Victorian to Modernist Thought in the South, 1919-1945</em> (UNC Press Books, 1982), 273. Orvin Lee Shiflett, <em>William Terry Couch and the Politics of Academic Publishing: An Editor’s Career as Lightning Rod for Controversy</em> (McFarland, 2015).</span><div>[15]</div></sup> No small task for a 24-year-old.</p> <figure><img src='img/figure1-3.jpg' alt='figure' onClick={() => props.handleImage(3)}/><figcaption><b>Figure 3.</b> A formal portrait of William "Bill" T. Couch. "William T. Couch (1901-1989)," Carolina Story: Virtual Museum of University History, accessed January 20, 2022, <a target="_blank" rel="noopener noreferrer" href="https://dc.lib.unc.edu/cdm/ref/collection/vir_museum/id/471">link</a>. </figcaption></figure> <p>Young, assertive, and constantly walking a fine line between boorish, arrogant, and visionary, Couch brought an intense interest in the region’s working class, shaped by his upbringing on the farm. More broadly, he was passionate about the future of the South, which required understanding its contemporary social conditions, an ideal project for a press charged with publishing cutting-edge scholarship on the region. This sociological interest, paired with a commitment to Southern liberalism, placed him directly in conversation and at times contestation with acclaimed scholar Howard W. Odum and his newly formed institute next door.</p> </Collapsible> <Collapsible trigger="A Center for Sociological Knowledge" overflowWhenOpen="visible"> <p>Odum was indicative of the research aspirations of the university, yet his job title reflected its past: University President Harry W. Chase recruited him in 1920 as the Kenan Professor of Sociology. Odum’s appointment signaled an institution in flux as his research was at odds with William Kenan, the name behind this endowed chair. Born in North Carolina, Kenan was a Confederate Civil War veteran and served briefly on UNC’s Board of Trustees. He later became infamous for his participation in the Wilmington Massacre of 1898 in which White leaders in the Southern Democratic Party led a coup d’état against the local government that resulted in the murder of White and Black citizens and harkened in a repressive white supremacist government.<sup className="tooltip"><span>David S Cecelski and Timothy B Tyson, <em>Democracy Betrayed: The Wilmington Race Riot of 1898 and Its Legacy</em> (UNC Press Books, 2000); "UNC Will Remove Plaques at Kenan Stadium Honoring Kenan Family Member Who Had Ties to Wilmington Massacre," <em>WralSPORTSfan</em>, October 3, 2018, <a target="_blank" rel="noopener noreferrer" href="https://www.wralsportsfan.com/unc-will-remove-plaques-at-kenan-stadium-honoring-kenan-family-member-who-had-ties-to-wilmington-massacre/17891899/">[link]</a>.</span><div>[16]</div></sup> Odum’s progressive research combined with an endowed professorship named after this unapologetic white supremacist was emblematic of conflicting impulses within the university.</p> <p>Born in Georgia, Odum received a BA from Emory College in 1904, a MA from the University of Mississippi in 1906, a Ph.D. in psychology from Clark in 1909, and a Ph.D. in sociology from Columbia University in 1910. He taught at the University of Georgia and then served as Dean at Emory College from 1919-1920 before arriving at UNC.<sup className="tooltip"><span>Sanders, <em>Howard W. Odum’s Folklore Odyssey</em>.</span><div>[17]</div></sup> His research agenda used scientific methods to study folklore and music of the South and was marked by a progressive approach to race relations. His progressive stance was largely informed by the friendships he made while conducting research with African American communities.<sup className="tooltip"><span>Ibid, ix–x.</span><div>[18]</div></sup> Odum was in a position of power as an endowed professor in charge of the newly formed Department of Sociology and of the School of Public Welfare, which would become known as the School of Social Work.</p> <p>In 1924, Odum opened the Institute for Research in Social Science (IRSS), which would bring together and build the careers of some of the most important scholars of social life of the 20<sup>th</sup> century, including Arthur Raper and Rupert Vance. The creation of IRSS (which eventually would be renamed the Odum Institute in its founder’s honor) signified an important investment in scholarly inquiry about social relations in the South and helped to modernize the new South through intellectual thought that championed liberal ideas in politics and race relations.<sup className="tooltip"><span>Ibid, 4.</span><div>[19]</div></sup></p> <p>IRSS scholars published hundreds of books and articles, mostly through their journal <em>Social Forces</em> and the UNC Press, both of which Odum helped launch in 1922.<sup className="tooltip"><span>The journal was originally named the <em>Journal of Social Forces</em> and changed to <em>Social Forces</em> in 1925.</span><div>[20]</div></sup> Odum and Couch recognized that their organizations were not only positioned to be local thought leaders but to shape how the nation understood the region. Moreover, IRSS’s funding was critical to the financial solvency of UNC Press and, consequently, made Odum a powerful voice in the direction of the university press, which often placed him in direct conflict with the fellow White Southerner almost two decades his junior. The stakes of their agreements <em>and</em> disagreements heightened as bureaucrats, intellectuals, and the broader public debated the future of the South during a time of economic, cultural, and social turmoil that severe global economic depression exacerbated.</p> </Collapsible> <Collapsible trigger="The Great Depression: A Nation in Turmoil" overflowWhenOpen="visible"> <p>The financial crash on October 24, 1929, which became known as Black Tuesday, had been decades in the making. While the economy grew after the depression of the 1890s and World War I, recurring economic panics following the Civil War served as regular reminders of the US banking system’s vulnerabilities.<sup className="tooltip"><span>Michael E McGerr, <em>A Fierce Discontent: The Rise and Fall of the Progressive Movement in America, 1870-1920</em> (Oxford University Press, USA, 2005).</span><div>[21]</div></sup> Already feeling the impact of global agriculture markets as prices waxed and waned, conditions for agricultural workers only worsened. The nation’s history of settler colonialism resulted in US citizens pushing westward, colonizing native people’s lands, and pursuing agriculture on precarious ground.<sup className="tooltip"><span>Ned Blackhawk, <em>Violence over the Land: Indians and Empires in the Early American West</em> (Harvard University Press, 2009); Kathleen DuVal, <em>The Native Ground: Indians and Colonists in the Heart of the Continent</em> (University of Pennsylvania Press, 2011); Richard Edwards, Jacob K Friefeld, and Rebecca S Wingo, <em>Homesteading the Plains: Toward a New History</em> (U of Nebraska Press, 2017); Jean M OBrien, <em>Firsting and Lasting: Writing Indians out of Existence in New England</em> (U of Minnesota Press, 2010); Noenoe K Silva, <em>Aloha Betrayed: Native Hawaiian Resistance to American Colonialism</em> (Duke University Press, 2004).</span><div>[22]</div></sup> Millions of acres of land due to over-farming and drought left their plows still, and by the 1930s resulted in millions of people were migrating in search of subsistence.</p> <figure><img src='img/figure1-4.jpg' alt='figure' onClick={() => props.handleImage(4)}/><figcaption><b>Figure 4.</b> Families migrating from Oklahoma to New Mexico during the Great Depression. Lange, Dorothea. Drought refugee families from Oklahoma on road to Roswell, New Mexico, to chop cotton. Near Lordsburg, New Mexico. 1937. Negative. Farm Security Administration - Office of War Information Photograph Collection (Library of Congress), <a target="_blank" rel="noopener noreferrer" href="https://www.loc.gov/pictures/item/2017769977/">link</a>. </figcaption></figure> <p>Despite the signs, the general sentiment of the Coolidge and the Hoover administrations was that laissez-faire economic policies were working. Government intervention in business and financial markets was curtailed as taxes were reduced, and isolationism was the proclaimed strategy for foreign policy, even as the US significantly meddled with and reshaped Latin America. Hoover planned to continue the course until Black Tuesday reminded the nation that its financial institutions remained on precarious ground. Years of unsustainable speculative capital resulted in the crash of the stock market and an economic crisis that quickly moved across the Atlantic. The exigencies of agricultural markets, world war, and capitalism meant too little too late to prevent environmental and economic destruction.<sup className="tooltip"><span>McGerr, <em>A Fierce Discontent: The Rise and Fall of the Progressive Movement in America, 1870-1920</em>.</span><div>[23]</div></sup> By 1932, unemployment surpassed 12 million.<sup className="tooltip"><span>Penkower, <em>The Federal Writers’ Project ; a Study in Government Patronage of the Arts</em>, 2.</span><div>[24]</div></sup> The ripple through the economy would leave almost one in four Americans unemployed. Millions of Americans were challenging Hoover’s approach to governance and looking to sign-up for a different path.</p> <p>Central to national debates about how to end the depression were questions about the role of the federal government and its ability to care for its citizens. By the time Roosevelt came into office in 1933, he was a seasoned veteran of US politics and attuned to the changing will of Americans. His election was built on his strong rebuke of 1920s laissez-faire policy and embrace of Progressive Era commitments such as the federal government’s active role in spurring, regulating, and reforming American labor and business. Roosevelt’s administration advocated for extensive federal intervention in the economy and providing social services to alleviate the effects of the depression. Such policies were possible because of a shift in the nation’s willingness to expand federal power if it meant relief and reform. Exactly how the government should intervene, which branches of the government had authority for which tasks, which policies and reforms to implement, and for whom occupied national debates. The expansion of federal regulatory power through an activist government that regulated the economy and society became a central tenet of his administration’s signature policies known collectively as the New Deal and its underlying political philosophy as New Deal liberalism. Intellectuals from academia, including Couch and Odum, were active participants.</p> </Collapsible> <Collapsible trigger="The Great Depression: Problem of the South" overflowWhenOpen="visible"> <p>In the South, the Great Depression exacerbated an economy mired by exploitative agricultural and industrial capitalists who created poor working conditions, offered low wages, and used the logic of white supremacy to maintain power. Intellectuals, including Couch and Odum, argued over how to assess and understand the impact of global depression on the region and which paths would best alleviate the conditions, a debate informed by a fraught relationship between federal intervention and the region’s White leadership. Federal government leadership and northern intellectuals alike understood the region as lagging behind the rest of the nation. Seen as impervious to change, the South came under a microscope as stereotypes abounded of an anti-modern, depraved, and uneducated region. The South’s economic woes, seen as exceptional and pervasive even by contemporary standards, combined with a culture of segregation enforced through Jim Crow laws and spectacular racial violence, left questions about the region’s fitness for full inclusion and citizenship.<sup className="tooltip"><span>Hale, <em>Making Whiteness: The Culture of Segregation in the South, 1890-1940</em>; Gilmore, <em>Gender and Jim Crow: Women and the Politics of White Supremacy in North Carolina, 1896-1920</em>.</span><div>[25]</div></sup></p> <figure><img src='img/figure1-5.jpg' alt='figure' onClick={() => props.handleImage(5)}/><figcaption><b>Figure 5.</b> A segregated bus station in Durham, North Carolina in 1940. Delano, Jack. Bus station in Durham, North Carolina, 1940. Farm Security Administration - Office of War Information Photograph Collection (Library of Congress), <a target="_blank" rel="noopener noreferrer" href="https://www.loc.gov/pictures/item/2017747600/">link</a>. </figcaption></figure> <p>The Great Depression further animated regional debates about the character and future of the South among intellectuals residing in the region’s universities. The entanglement of the New Deal state and literary market with academics meant scholarship from academia impacted government policy and ideas about the region. Couch and Odum sought to position themselves at the center of these regional debates through the scholarship that they produced. While they argued for different approaches to understanding social conditions in the region, both were liberal progressives who were critical but still supportive of modernization. Neither was eager to romanticize Southern "tradition." Their positioning placed them in debate with a powerful set of intellectuals based at another Southern university on the ascent, Vanderbilt University.</p> </Collapsible> <Collapsible trigger="Southern Agrarians vs Regionalists" overflowWhenOpen="visible"> <p>Questions about how to provide relief to and reform the South spurred a deep rift in academic and literary thought, provoking the intellectual community into two warring schools of thought: Southern Agrarians and Regionalists. Agrarians were best represented through Vanderbilt University academics John Crowe Ransom, Allen Tate, and Donald Davidson. Their 1930 manifesto, <em>I’ll Take My Stand,</em> renounced modernization through industrial capitalism. "The younger southerners, who are being converted frequently to the industrial gospel, must come back to the support of the Southern tradition," they wrote, adding that "they must be persuaded to look very critically at the advantages of becoming a ‘new South’ which will be only an undistinguished replica of the usual industrial community."<sup className="tooltip"><span>Southerners, <em>I’ll Take My Stand: The South and the Agrarian Tradition</em>.</span><div>[26]</div></sup> Looking to save "traditional Southern life," they advocated for a return to agrarian life and the "culture of the soil." Such calls for a return to traditional values were made without any discussion of the institutionalized systems of slavery that shaped such "tradition."</p> <p>Southern Agrarians also felt that Northern progressive thought had infiltrated university halls in the South with their ideas about modernization through industrialization and consumerism. They particularly turned their scrutiny toward the emerging field of Sociology. Agrarians argued that sociological scholarship, much of which was published by UNC Press, aided in the erosion of Southern traditional values. Davidson saw "the sociologist [as] the twentieth-century successor to the nineteenth-century abolitionist. A disturber of the peace and the status quo, he abhorred the concrete, the organic, the religious, and preferred the abstract, the theoretical, and the scientific. Indeed, so blinded by charts, tables and statistics was he that he could not see the flesh and blood individual."<sup className="tooltip"><span>As quoted in Fred Hobson, <em>Tell about the South: The Southern Rage to Explain</em> (LSU Press, 1983), 180–81.</span><div>[27]</div></sup></p> <p>As the poster child for Sociology and an advocate of "the New South," Howard Odum and his UNC-Chapel Hill colleagues became the Agrarians’ chief opponents and symbolized the regionalism school of thought.<sup className="tooltip"><span>Howard W. Odum, "Promise and Prospect of the South: ‘A Test of American Regionalism,’" <em>Proceedings of the Annual Session (Southern Political Science Association)</em>, no. 8 (1935): 8–18.</span><div>[28]</div></sup> Many of the "Chapel Hill Sociologists," which they were labeled, may have grown up and worked in the South, but they were not reactionary romantics of the Old South like the Agrarians. While Odum and his colleagues saw great problems in the region, they advocated for systematic, scientific studies of the region so that solutions to social problems could be found, rather than calling for a return to tradition as did the Agrarians.</p> <p>Though Odum is often referred to as the founder of this sociological approach of regionalism that centered scientific data to address social problems, his work along with his colleagues is directly indebted to WEB. Du Bois and the Atlanta School. Du Bois began working at the Atlanta Sociological Laboratory in 1897 at what is now known as Clark Atlanta University. Under Du Bois’ direction, the lab was the first to collect what was understood as objective, scholarly data about Black communities in the South.<sup className="tooltip"><span>Earl Wright, "WEB Du Bois, Howard W. Odum and the Sociological Ghetto," <em>Sociological Spectrum</em> 34, no. 5 (2014): 453–468.</span><div>[29]</div></sup> Their pioneering sociological methods produced data that challenged racist pseudoscience representations of Black communities and suggested improvements.<sup className="tooltip"><span>Whitney Battle-Baptiste and Britt Rusert, eds., <em>W. E. B Du Bois’s Data Portraits: Visualizing Black America</em> (Princeton Architectural press, 2018).</span><div>[30]</div></sup> Despite this much earlier and significant advance, Du Bois is rarely credited as the creator of modern American Sociology, regional methods, or the sociology of the South.<sup className="tooltip"><span>Aldon Morris, <em>The Scholar Denied</em>: <em>W. E. B. Du Bois and the Birth of Modern Sociology</em> (University of California Press, 2017).</span><div>[31]</div></sup></p> <p>While Odum did not center Du Bois’ studies in his own work, he drew on many of his ideas, which guided the development of UNC’s Institute for Research in Social Science. Odum believed that addressing the many problems of the South began with better understanding its unique regional culture. He argued, "cataloging the traits of the South was the first step toward merging the region with the rest of the nation while maintaining its distinctive culture."<sup className="tooltip"><span>Sanders, <em>Howard W. Odum’s Folklore Odyssey</em>, 6.</span><div>[32]</div></sup> Therefore, his studies were dedicated to this endeavor of cataloging all types of Southern traits, including folklore, health, technology, and eating habits. His studies painted a dismal picture of an impoverished South in need of economic and cultural reform. To remedy these problems, Odum argued for adapting agricultural ways of life with industrial life to create a "‘new equilibrium between rural and urban’" in order to better integrate into the larger nation.<sup className="tooltip"><span>Odum, "Promise and Prospect of the South," 16.</span><div>[33]</div></sup> Additionally, he called for improving "race relations through education" but did not go as far as to call for an end to segregation.<sup className="tooltip"><span>Bob Holladay, "The Gods That Failed: Agrarianism, Regionalism, and the Nashville-Chapel Hill Highway," <em>Tennessee Historical Quarterly</em> 64, no. 4 (2005): 300.</span><div>[34]</div></sup></p> </Collapsible> <Collapsible trigger="Couch and the Ecosystem of Publishing" overflowWhenOpen="visible"> <p>Amid this debate between Agrarians and Regionalists, Couch came into his intellectual own. While he was distressed over the portrayals of the region as backward and retrograde, Couch did not desire to romanticize or call for a return to "traditional Southern life" like the Southern Agrarians. He believed that romanticizing the region was as unproductive as vilifying the region, for neither helped to identify the very real issues that left millions in poverty nor the possibilities for reforms. He worried that such facile and tired stereotypes of the region risked characterizing the South as beyond reform and change. Instead, he argued that an emphasis on authenticity and realism would hold a mirror up to the South, forcing it to acknowledge the social conditions of the region, which included class animosity spurred by industrial capitalism, a social order maintained by racial violence, and economic conditions often producing poverty. Publishing cutting-edge work of scholars who were experts in assessing social conditions to reshape the debate and characterizations of the region became a raison d’etre for Couch and UNC Press, helping to produce a cultural shift on two fronts. First, the Press showed that Southerners themselves possessed a critical lens about the region, eventually publishing over 450 titles under Couch’s tutelage. Second, the Press actively reshaped how the nation understood the region, rather than simply following or responding to others. The efforts were not just local or regional.</p> <p>UNC Press participated in a larger growing ecosystem of commercial and university presses shaping how intellectuals, policymakers, and the reading public understood the South. The commercial book industry grew substantially in the 1920s and 1930s, led by some of the most prominent publishing houses such as Houghton, MacMillan, and Viking and magazines such as the <em>New York Times</em>, <em>Atlantic</em>, and <em>Nation</em>. Centered in New York City, the commercial publishing industry’s primary audience was the White, urban middle, and upper class. While sales slowed, book publishing actually maintained a solid footing during the 1930s. One area that caught the attention of publishers, critics, and readers was the South. The publishers noted consumers’ – mostly bourgeois urban White consumers residing in urban communities – interest in the exotic and ‘Other;’ places and people outside of their social and cultural milieu. In many ways, such an interest helped spur the Southern Literary Renaissance.<sup className="tooltip"><span>Sarah Gardner, <em>Reviewing the South</em> (Cambridge University Press, 2017).</span><div>[35]</div></sup></p> <p>Importantly, the commercial book industry did not shy away from work that addressed issues in the South, much to the chagrin of the Agrarians who saw Southern presses as complicit in the erosion of Southern values. Agrarian champion, Donald Davidson, included publishing in the "great Northern offensive of the 1920s," which began during the Harding Administration and aimed to attack "Southern life and its characteristic institutions." He argued that:</p> <blockquote> <p>This attack was more abusive and unrelenting than anything the Southern states have experienced since the last Federal soldier was withdrawn from their soil. There was no single institution, like slavery, in the nineteen-twenties, upon which attacks could be centered. They had a vaguer objective in the so-called backwardness, or ‘cultural lag,’ of the South. The Northern Press, with all of the Southern Press that takes its cue from New York…unanimously agreed that the South [was] guilty of number crimes against progress…’ ultimately creating ’an image of the benighted South, a savage South of racial hatred and religious fanaticism.<sup className="tooltip"><span>As quoted in Hobson, <em>Tell about the South: The Southern Rage to Explain</em>, 183.</span><div>[36]</div></sup></p> </blockquote> <p>Entering the literary marketplace were books by scholars such as Howard Odum, who identified and addressed the South’s problems, novelists such as Erskine Caldwell and Grace Lumpkin, as well as magazine articles on the very topics these books discussed, such as mill workers’ labor struggles and the conditions of tenant farming.<sup className="tooltip"><span>Gardner, <em>Reviewing the South</em>, 9.</span><div>[37]</div></sup> As a result, the commercial Press became a site where reformist messages that called for changes to oppressive conditions in the South, such as racial violence, poverty, and the industrial-capitalist order, took precedence over agrarian romanticism. These publishers were the precise Northern presses that the Agrarians railed against as they saw the presses as demonizing the South and its culture.</p> </Collapsible> <Collapsible trigger="Publishing Scholarship About the South" overflowWhenOpen="visible"> <p>While there had been fits and starts in the United States, the early 1900s saw the ascendency of university presses with the rise of the research university. While commercial publishers were publishing more books about the region, they were still few and far between compared to the scholarly output of Southern academics, which UNC Press harnessed. By the 1930s, academic publishing through a university press was in vogue. Institutions like Johns Hopkins University argued that knowledge should not be limited to those who could participate in the daily life of the university but rather should be more accessible to the broader public.<sup className="tooltip"><span>Peter Givler, "University Press Publishing in the United States," Association of University Presses, accessed September 23, 2019, <a target="_blank" rel="noopener noreferrer" href="http://www.aupresses.org/about-aaup/about-university-presses/history-of-university-presses">[link]</a>.</span><div>[38]</div></sup> One mechanism of dissemination was publishing. However, commercial publishers knew the audience for scholarly works was marginal, particularly regarding potential profits. On the other hand, university presses were non-profits and had major institutions, some of which would become the most affluent non-profits in the world by the 21<sup>st</sup> century behind them.</p> <p>When Couch took the reins of UNC Press, he was unamused with the university press landscape. Looking to court rather than shy away from contentious and controversial topics and ideas, Couch viewed other university presses as safe and cautious. "There is much in them which should be a warning and an example to us," he stated, adding that "if the University Press, like Harvard or Yale, is to devote itself to bringing out nice inoffensive books—perfect examples of modern scholarship—it seems to me that the legislative gentlemen who protest at our expenditures have a real reason for their protests."<sup className="tooltip"><span>Daniel Joseph Singal, <em>The War Within: From Victorian to Modernist Thought in the South, 1919-1945</em> (UNC Press Books, 1982), 273.</span><div>[39]</div></sup> If anything, Couch believed the Press should be more critical to further rigorous intellectual thought about the region.<sup className="tooltip"><span>Singal, 274.</span><div>[40]</div></sup> Publishing could be a form of intellectual activism, and playing it safe was a conservative stance that silenced rather than fostered intellectual inquiry.<sup className="tooltip"><span>Orvin Lee Shiflett, <em>William Terry Couch and the Politics of Academic Publishing: An Editor’s Career as Lightning Rod for Controversy</em> (McFarland, 2015).</span><div>[41]</div></sup> Such a philosophy helped turn UNC Press into an intellectual leader known for publishing accessible, innovative, and often contentious scholarship, but put Couch regularly at odds with his board of directors and university leadership, including Howard Odum.<sup className="tooltip"><span>Singal, <em>The War Within</em>, 278.</span><div>[42]</div></sup></p> <p>While Howard Odum and Couch’s relationship soured over time, Couch valued the cutting-edge research on the social and economic problems of the South coming from the Institute for Research in Social Science. UNC Press became their publishing house, printing 31 books from the institute between 1924-1934.<sup className="tooltip"><span>"History of the Odum Institute," The Odum Institute for Research in Social Science, accessed September 23, 2019, <a target="_blank" rel="noopener noreferrer" href="https://odum.unc.edu/about/history/">[link]</a>.</span><div>[43]</div></sup> Acclaimed sociologists Arthur Raper and Rupert Vance produced studies on areas including relations, labor relations, government, and Southern history.<sup className="tooltip"><span>Singal, <em>The War Within</em>, 276.</span><div>[44]</div></sup> Arthur Raper’s <em>The Tragedy of Lynching</em> (1933), for example, used case studies to illustrate the prevalence of lynching, describe how White Southerners justified this form of vigilante violence and explain the cultural and economic impact of lynching on the region.<sup className="tooltip"><span>Arthur F. Raper, "Mass Violence in America: The Tragedy of Lynching," 1933.</span><div>[45]</div></sup> Reviewing the book in the <em>Journal of Negro History</em>, Rayford W. Logan offered praise for the book calling it "one of the most notable contributions to the literature about America’s greatest shame" and noted that such a book came from a Southern press. He wrote, "Of more than passing interest is the fact that a book so condemnatory of the South should be published by the University of North Carolina Press, although this is by no means the first time that this publishing house has brought out books that do not portray that section as the domicile of chivalry."<sup className="tooltip"><span>Rayford W. Logan, review of <em>The Tragedy of Lynching</em>, by Arthur Raper, <em>The Journal of Negro History</em> 18, no. 4 (1933): 484–86, <a target="_blank" rel="noopener noreferrer" href="https://doi.org/10.2307/2714309">[link]</a>.</span><div>[46]</div></sup> Works like Raper’s garnered acclaim and ire for the Institute and UNC Press.</p> <p>Like Odum, Couch shared a concern for the region’s significant challenges. Odum, alongside university leadership, including President Harry W. Chase and prominent faculty such as Edwin Greenlaw, founded UNC Press with visions that the Press would be the center of intellectual life in the region by addressing pressing concerns. However, the rift between Odum and Couch began to grow as time passed. Complicated by the Press’s initial financial dependency on the IRSS, Odum and Couch increasingly disagreed over audiences and what kinds of knowledge could reveal the region’s challenges. It did not help that Couch was disinterested in the quantitative turn underway in Sociology, spurred by the field’s interest in securing a position as a science. Couch believed in communicating to a broader audience than academics, necessitating a strategy other than dense specialized prose or faceless statistics.<sup className="tooltip"><span>Singal, <em>The War Within</em>, 271.</span><div>[47]</div></sup> After all, he was earning a national reputation for publishing some of the most influential scholarship on the region because of his belief that books should be accessible to a range of audiences.<sup className="tooltip"><span>Shiflett, <em>William Terry Couch and the Politics of Academic Publishing: An Editor’s Career as Lightning Rod for Controversy</em>; Singal, <em>The War Within</em>.</span><div>[48]</div></sup></p> </Collapsible> <Collapsible trigger="An Opportunity" overflowWhenOpen="visible"> <p>Couch noticed an opportunity to intervene in the larger literary market by publishing works that could appeal to a broader reading public that had stoked the Southern Literary Renaissance. Readers and Southern writers were no longer solely wading in the violent and stale waters of Southern exceptionalism and Lost Cause romanticism. They were looking for fresh, realistic, and critical perspectives on Southern culture and society. Yet, Couch had developed reservations about the work produced as a part of the Southern Literary Renaissance. He was concerned that its focus on fiction did not fully capture the conditions of the South or the lived realities of its people. UNC Press, he reasoned, was positioned to reach the same audience with books that were grounded in rigorous scholarly inquiry written in inviting nonfiction prose.</p> <p>With national reputations and success in their respective areas, the rift between Couch and Odum grew even deeper. While Couch was persuaded by sociology’s focus on social systems that led to societal problems, his skepticism of the very way sociology produced knowledge mounted. He understood the reliance on statistics rather than thick description as obfuscating the lived realities of people. However, he saw more promise in sociology’s method known as case history, which analyzed a single person’s life in detail, but that method, too, had its problems. Case history usually focused on people identified as deviant and left little room for the subject’s own assessment of their life. Moreover, regardless of the method, Couch believed that scholarship in sociology purposefully produced prose that was less accessible to a more general public, which represented a significant shortcoming in motivating the public to address identified social problems. Couch was troubled over how to effectively convey the social conditions of the region to convey the complexities of life in the South when a new door opened.</p> <p>Roosevelt’s administration engaged in an incredible expansion of federal power to bring about relief, reform, and recovery from the Great Depression. Cultural workers, including intellectuals and writers, would become part of "The New Deal" through a new agency called the Federal Writers Project (FWP). Couch quickly rose through the ranks. The rise of the New Deal and the FWP shaped Couch’s efforts to remake sociological knowledge about the South by giving him and his colleagues an opportunity to produce a new method of documenting and writing that centered the people’s perspective. The FWP offered another institutional structure, the federal government, alongside the university and UNC Press to experiment and realize a new method at the intersection of literature and sociology for understanding the region by 1936.</p> </Collapsible> </div>
    )
}

export {Layer2};
