
import React from 'react';

// eslint-disable-next-line
import Collapsible from 'react-collapsible';

// eslint-disable-next-line
import {ChangeMapLink, ChangeTopicLink} from './utils';

function Layer7(props) {

    return(
    <div className="text-holder"> <h1 id="method">Method</h1> <p>This layer provides a description of the methods used to collect the data for the project, the models used to perform text analysis of the life histories, and the underlying technologies used to create the digital project. All of the code and data are available for download under permissive open-source licenses. Links are provided within the text.</p> <Collapsible trigger="Data Collection" overflowWhenOpen="visible"> <p>The collection of life histories used in this project are held by The Wilson Library at the University of North Carolina at Chapel Hill in the Southern Historical Collection.<sup className="tooltip"><span>Federal Writers’ Project papers #3709, Southern Historical Collection, The Wilson Library, University of North Carolina at Chapel Hill.</span><div>[1]</div></sup> The texts of the life histories have been digitized and made available for public access through the library’s web interface. Life histories are provided as PDF images; there are no searchable, machine-readable versions of the texts available on the website.<sup className="tooltip"><span>For more, see <a target="_blank" rel="noopener noreferrer" href="https://catalog.lib.unc.edu/catalog/UNCb2431314">[link]</a>.</span><div>[2]</div></sup> Most life histories have a short caption that includes information about the title of the interview, its location, date, and interviewee’s name. This information is not structured into specific fields. Additional metadata information about the life histories are included in the digitized images in the form of headers at the top of most interviews and as summary cards included in the archive. We structured the headers into a database for this project and created plain text machine-readable versions of each life history for data analysis.</p> <p>Creating metadata records of the interviews required manually parsing the unstructured text and reading the individual metadata headers for each interview. The process of manual parsing was conducted by the authors, students in a class taught by Rivard, and a paid research assistant. An article focused on the pedagogical practices and lessons learned from this process appeared in <em>Digital Humanities Quarterly</em>.<sup className="tooltip"><span>Courtney Rivard, Taylor Arnold, Lauren Tilton. "Building Pedagogy into Project Development: Making Data Construction Visible in Digital Projects." Digital Humanities Quarterly, 13.2 (2019).</span><div>[3]</div></sup> The article discusses various ways of crediting contributors—particularly how to credit those students who made substantive contributions beyond that of the typical class requirements—through authorship credit and other visualizations.</p> <p>After being converted into structure records, the metadata was organized into a collection of normalized tables.<sup className="tooltip"><span>We recognize that there are debates about the term "normalized." In this context, we use the word to indicate a specific approach to structuring, which is referred to as "database normalization."</span><div>[4]</div></sup> These tables contain information about writers, revisers, interviewees, interviews, and professions. The collection of tables follows the "tidy data" model, with a different table dedicated to each type of record.<sup className="tooltip"><span>Hadley Wickham, "Tidy data," <em>The Journal of Statistical Software</em>. 59.10 (2014).</span><div>[5]</div></sup> Normalized relational tables are particularly important in our dataset because many of the relationships linking tables to one another are complex one-to-many and many-to-many relationships. For example, most writers wrote more than one interview, and some interviews were co-written by two or more writers. The normalized data model guarantees that data about each entry is consistent and easy to update.</p> <p>While most of the variables in the metadata tables are relatively straightforward to record and describe, a few fields require some discussion. Some interviews used pseudonyms for the interviewee names. In many cases, the pseudonyms and real names are mapped to one another in the header of the typed interview; the digital archive hosted by The Wilson Library lists interviews by real names, not pseudonyms. For these two reasons, we have listed both forms of names when given in our dataset and use real names when displaying records on the digital site. This makes our data consistent with the archival source and does not reveal private data that is not available elsewhere.</p> <p>Recording race and gender information about writers and interviewees also require careful decision-making. As digital humanities scholars such as Catherine D’Ignazio and Lauren Klein have argued, "what gets counted counts," yet it must be done with careful attention to binaries, hierarchies, and classifications.<sup className="tooltip"><span>"What Gets Counted Counts." (2020). In Data Feminism. Retrieved from <a target="_blank" rel="noopener noreferrer" href="https://data-feminism.mitpress.mit.edu/pub/h1w0nbqp">[link]</a>.</span><div>[6]</div></sup> Because we were interested in understanding the gender and racial logic that the SLHP produced, we used the categories in the archival records.</p> <p>The SLHP subscribed to a gender binary of "male" or "female", which they often typed in the header of the life history. The gender of the interviewees and writers was inconsistently documented, though. Since we were interested in gender representation among the interviewees and writers, we assigned a gender based on the binary logic of the SLHP. Given how writers and editors wrote and edited the stories, the gender can usually be inferred based on pronouns and other gendered language (i.e., "wife") used in relation to an interviewee. The gender of the writers was determined by archival records through a close reading of correspondences for an individual’s pronouns. Given the number of writers who are silent in the archive except for the life history they wrote, we also turned to census data that used the gender binary.</p> <p>Along with gender, we were interested in how race was configured at the time. We took as our guide cautions about encoding racial logic through data that digital humanities scholars such as Jessica Marie Johnson and D.H. initiatives such as #transformdh and #dhpoco have elucidated.<sup className="tooltip"><span>Jessica Marie Johnson; Markup Bodies: Black [Life] Studies and Slavery [Death] Studies at the Digital Crossroads. <em>Social Text</em> 1 December 2018; 36 (4 (137)): 57–79. DOI: <a target="_blank" rel="noopener noreferrer" href="https://doi.org/10.1215/01642472-7145658">10.1215/01642472-7145658</a>. See also Earhart, Amy E., and Toniesha L. Taylor. "Pedagogies of Race: Digital Humanities in the Age of Ferguson." Debates in the Digital Humanities, JSTOR, 2016, pp. 251–64; Mahoney, Jennifer, et al. "Data Fail: Teaching Data Literacy with African Diaspora Digital Humanities." Journal of Interactive Technology &amp; Pedagogy, Dec. 2020; Noble, Safiya Umoja. "Toward a Critical Black Digital Humanities." Debates in the Digital Humanities, 2019, pp. 25–35.</span><div>[7]</div></sup> As constructed and unstable categories, racial categorization was a significant site of contention in the 1930s as groups debated the names and boundaries of race and ethnicity. For interviewees, we used the categories described in the interview metadata. The SLHP, we learned through the process of creating this data, used three primary categories: Negro, White, and Other. Because of the implications of the term "negro" in the past and today, we used "Black" as the category that appears on the site. The categories, and process of creating the data, revealed how the SLHP writers primarily sought life histories along a Black/White binary or, in the words of W.E.B. DuBois, along "the color line" of the segregated South.<sup className="tooltip"><span>DuBois, <em>Soul of Black Folk</em>, 5</span><div>[8]</div></sup> The category of "Other" indicated the disinterest in further classifying and identifying groups in the region. They were literally the other compared to the primary focus: people who resided in the region who identified along a Black/White binary.</p> <p>In an effort to account for the complicated relationship between race and ethnicity, they at times included categories such as Greek or Swedish. These categories should be used carefully, however, because the labels are inconsistently applied. For example, interviews of Greek families sometimes describe the interviewees as "White" and other times as "Greek." However, the focus on ethnic communities is, for the most part, specific projects that were commissioned by an outside group or repurposed from another FWP project, particularly from Social-ethnic surveys. Drawing on concepts such as data feminism to use data to reveal marginalized voices, we included an additional field to capture more granular ethnic categories that can be inferred from the text.<sup className="tooltip"><span>These are not exhaustive, focusing most on flagging a few interviews of people of Greek and Cuban descent.</span><div>[9]</div></sup> We recognize that one must approach with caution the process of racial and ethnic categorization, but we hope that the data set at least offers meta data that disrupts the Black/White binary. We return to Klein and D’Ignazio’s point that "what gets counted counts" and queries to this data set by ethnicity will render visible stories that were not prioritized.</p> <p>The racial logic of the SLHP extended to the hiring and assignments of writers. Writers who were identified as White were allowed to interview anyone, and writers who were identified as Black were primarily assigned to interview those who identified as Black. Racial information for the writers was determined by archival records. Records for which we were unable to determine a racial category are labeled as "unclear." As a result, the metadata about the interviews mostly reflects the logic of the SLHP and 1930s because the aim of this project was to understand the gender and racial formations of the SLHP that then shaped whose stories were (and were not) told and how.</p> <p>Our dataset includes a record for every interview that is listed in the archive’s finding aid. The digital files are organized into folders, which most frequently contain a single interview, but occasionally include over a dozen individual interviews. Some interviews are duplicates or near-duplicates of each other, and others consist of a single sentence indicating that the record has been deleted. For consistency and simplicity, our data split out each individual interview and includes duplicate records. Removal of duplicates and other processing is done during the analysis of the data, making explicit how modeling decisions relating to the data contained in the archive.</p> <p>Along with the metadata, we also produced machine-readable versions of the text of each interview. Off-the-shelf optical character recognition (O.C.R.) made a reasonable first pass of some of the interviews but produced unusable text in others. An external paid service was used to manually clean up the O.C.R. into usable text. Some typed records contain either typed or handwritten corrections made by the revisers and editors in the Southern Life History Project; these include fixing typos, rephrasing sentence structure, editorial comments about the content and quality of writing, and making substantive edits to the content of the interview. For consistency, and because the crossed-out text was often unreadable, the machine-readable files used only the corrected versions of the text without any handwritten editorial comments. In a limited number of cases, due to physical imperfections, fading, or issues with the digitization process, small portions of the texts were unreadable. These are marked with the phrase "[text not clear]." The final machine-readable text files include all of the header information contained in the typed pages but exclude page numbers and any written comments that are not corrections to the main text.</p> <p>The metadata and machine-readable versions of the life histories are published under the open-source GNU Public License (GPL-2).<sup className="tooltip"><span>Arnold, Taylor, Alexander, Emeline Blevins, Rivard, Courtney, Tilton, Lauren, &amp; Wexler, Laura. (2020). FWP Life History Project in the American South: Machine-Readable Text and Metadata (Version 1.0). Zenodo. <a target="_blank" rel="noopener noreferrer" href="http://doi.org/10.5281/zenodo.3865765">10.5281/zenodo.3865765</a>.</span><div>[10]</div></sup> These can be downloaded in bulk. All of the data are contained in plain text forms that can be read by most data analysis software. Metadata is provided as CSV files, and the texts are provided as text files (one file per interview). All of the material is encoded using UTF-8; the internal consistency of the records was checked with a set of unit tests.<sup className="tooltip"><span>The tests ensure, for example, that all of the table keys link together and that the interviews appear to correctly match their metadata (i.e., contains the title of the interview in the text and has a reasonable number of words given the number of pages).</span><div>[11]</div></sup> The points on the interactive map and embedded figures in this project were all created using this metadata.</p> </Collapsible> <Collapsible trigger="Map: Spatial Analysis" overflowWhenOpen="visible"> <p>The main visualization element when first landing on the project is a large interactive map of the southeastern United States. The default map shows the locations where interviews in the SLHP took place through the use of round dots overlaid on a minimal map of the United States. An analysis of the spatial elements of the collection shapes, in particular, the work in <span onClick={() => props.handlePage(4)}>Layer 3</span>, but is present throughout the entire project.</p> <p>Information about the location where each interview took place was included in the metadata discussed in the previous section. In the archival data, interviews have different levels of specificity of their spatial location. Almost all provide a state and city. Some include a neighborhood name or even an exact street address. For the map, we needed to associate each location with specific longitude and latitude coordinates. Because of the desired scale of the map, we worked only with city and state information. To accomplish the mapping to coordinates, we started by using a computational method through the GeoNames database.<sup className="tooltip"><span>Ahlers, Dirk (2013), "Assessment of the accuracy of GeoNames gazetteer data", Proceedings of the G.I.R. Workshop, pp. 74–81, CiteSeerX 10.1.1.722.8740</span><div>[12]</div></sup> When a city was found in the database, we used the listed longitude and latitude associated with the center of the city. After this process, we found two dozen cities that were not exact matches. For these, we manually figured out how to map the location to coordinates. Most of these were either small towns that no longer exist or unofficial locations (such as the name of a farm) that we were able to locate by looking closely at the interview text.</p> <p>On each of the interactive maps, there is a point for each location associated with an interview in a given category. The area of the point is proportional to the number of interviews in one location. The area of the point is fixed by the map distance, meaning that when the map is zoomed into, the circles become larger in absolute terms but cover (approximately) the same area of the map. We found through user-testing that this approach was much easier to follow and visually engaging at high levels of zoom. Points on the map are given an opaque color so that points for nearby towns can be seen even when one town has a large number of interviews. On the maps which are colored in different colors based on metadata, such as the prolific interviewers, there are a few cases where multiple colors would need to be associated with the same location. To allow visitors to click on both points, we ensure that the larger dot is behind, the smaller one. Further technical details about the construction of the interactive map in JavaScript are given in the Digital Platform section below.</p> </Collapsible> <Collapsible trigger="Themes: Text Analysis" overflowWhenOpen="visible"> <p><span onClick={() => props.handlePage(5)}>Layer 4</span>, alongside other archival evidence, presents several computational models to help understand and organize the 1248 life histories in the collection. For these analyses, duplicated interviews and interviews whose text was removed from the archive were not included in the analysis. When two slightly different versions of an interview appeared in the collection, we selected the longest text. After filtering, the collection contained 1106 life histories. Finally, these were cleaned to remove headers and instances of "[text not clear]" within the machine-readable text.<sup className="tooltip"><span>For more about debates on "cleaning" data, see Katie Rawson and Trevor Muñoz, "Against Cleaning" in <em>Debates in the Digital Humanities 2019</em> eds. Lauren Klein and Matt Gold. Minnesota: University of Minnesota, 2019. <a target="_blank" rel="noopener noreferrer" href="https://dhdebates.gc.cuny.edu/read/untitled-f2acf72c-a469-49d8-be35-67f9ac1e3a60/section/07154de9-4903-428e-9c61-7a92a6f22e51">[link]</a>.</span><div>[13]</div></sup></p> <p>The two text analysis models used in our analysis—topic modeling and document clustering—are both built to analyze term frequencies (T.F.s). T.F.s count how frequently particular words or word forms occur within each document within a corpus. To compute these counts, we passed the text of each life history through a language processing (N.L.P.) pipeline using the R package cleanNLP.<sup className="tooltip"><span>Taylor Arnold. A Tidy Data Model for Natural Language Processing Using cleanNLP The R Journal, 9.2 (2017): 248-26.</span><div>[14]</div></sup> The package applies pre-built models to split the text into individual words and punctuation marks (tokenization), construct standardized forms of the words (lemmatization), and tag each word with a part-of-speech code.<sup className="tooltip"><span>Honnibal, Matthew and Montani, Ines and Van Landeghem, Sofie and Boyd, Adriane. "spaCy: Industrial-strength Natural Language Processing in Python." 2020, Zenodo. 10.5281/zenodo.1212303.</span><div>[15]</div></sup> Using this information, we constructed counts of lemmas for all lemmas tagged as nouns, verbs, adjectives, and adverbs.<sup className="tooltip"><span>Bies, Ann, Justin Mott, Colin Warner, and Seth Kulick. "English Web Treebank LDC2012T13". <em>Linguistic Data Consortium</em>, 2012. <a target="_blank" rel="noopener noreferrer" href="https://doi.org/10.35111/m5b6-4m82">10.35111/m5b6-4m82</a>.</span><div>[16]</div></sup> Because of the heavy influence of place names in the lexicon of the texts, we also explicitly removed any place names (i.e., cities and census-designated places) contained in any of the geographic columns in the metadata.<sup className="tooltip"><span>These should have been removed by the part of speech tags, which have a separate category for proper nouns, but errors are common with this process, particularly when proper capitalization is not used (not uncommon indirect quotes in this corpus).</span><div>[17]</div></sup> The topic model and document cluster can be explored in the Theme Interface.</p> <p>One challenge with the life histories is the use of a significant amount of "eye dialect," in which words are intentionally misspelled to signal what are problematically called "non-standard" pronunciations.<sup className="tooltip"><span>Morrison, Toni. <em>Playing in the Dark: Whiteness and the Literary Imagination</em>. Harvard University Press, 1992.</span><div>[18]</div></sup> Examples include "git" for "get" and "wuz" for "was". The use of dialect is an interesting and important feature that we investigate in <span onClick={() => props.handlePage(5)}>Layer 4</span>. It offers insight into how spelling was used as a racial signifier and inculcated in white supremacist ideologies. At the same time, it dominates the signal within the topic models and document clusters, making it hard to detect other linguistic features. We have made available versions of our models with dialect included and with dialect removed for these reasons. To identify and remove dialect, we started by comparing each of the words identified by the N.L.P. pipeline with a "standard" spelling dictionary of American English and removed words that were not included.<sup className="tooltip"><span>Németh, L., Trón, V., Halácsy, P., Kornai, A., Rung, A., &amp; Szakadát, I. (2004). Leveraging the open-source ispell codebase for minority language analysis. <em>Proceedings of SALTMIL</em>, 56-59.</span><div>[19]</div></sup> Then, we looked at the most over-represented words in interviews of Black interviews and manually constructed a list of additional dialect terms to remove. These consisted of relatively uncommon terms that are English words but have alternative meanings. For example, the word "den" was used heavily in the corpus as dialect for the word "then." Using this approach as a strategy to explore other linguistic features, the approach is intended to offer another way to explore the topics that do not reduce the stories of people of color to primarily racist applications of dialect.</p> <p>Using the term frequencies, we computed two sets of topic models, one with dialect terms and one without them. We used latent Dirichlet allocation (LDA) as implemented by the R package topic models to construct the models.<sup className="tooltip"><span>Hornik, K., &amp; Grün, B. (2011). topic models: An R package for fitting topic models. <em>Journal of statistical software</em>, <em>40</em>(13), 1-30.</span><div>[20]</div></sup> LDA is a common and well-known technique in digital humanities and digital history research that calculates a probability for word co-occurrence.<sup className="tooltip"><span>For a good technical overview focused on humanities applications, see David Blei, "Topic Modeling and Digital Humanities," <em>Journal of Digital Humanities</em>, 2.1 (2012).</span><div>[21]</div></sup> After some experimentation, we included 16 topics to display. The visualization of these topics on the digital project includes the probability distributions over words and the probability distributions of documents that each model defines.</p> <p>Finally, document clustering was also applied to each of the two sets of term frequencies. Document clustering is the process of grouping all of the items in a corpus of texts into discrete groups, called clusters, based on linguistic features. Clustering is not commonly used in D.H. projects, primarily because of the momentum around the use of topic models. For many projects, including ours, it is useful to find groups of documents that use similar language features. It is possible to find some groups of documents by looking at the results of a topic model, but this approach will miss documents that cross between multiple topics. Also, the results of LDA are quite sensitive to a number of parameters, most notably the number of topics used. Document clustering can avoid these issues and move directly to the task of grouping together similar documents.</p> <p>We used spectral clustering to produce clusters of documents. Spectral clustering is a relatively well-known technique in statistical computing for grouping together textual documents.<sup className="tooltip"><span>William Donath and Alan Hoffman (1972). "Algorithms for partitioning of graphs and computer logic based on eigenvectors of connections matrices". <em>I.B.M. Technical Disclosure Bulletin</em>.</span><div>[22]</div></sup> We used the implementation from the R package casl to apply this algorithm.<sup className="tooltip"><span>Taylor Arnold, Michael Kane, Bryan Lewis. <em>A Computational Approach to Statistical Learning</em>. New York: Chapman &amp; Hall/C.R.C. Texts in Statistical Science (2019).</span><div>[23]</div></sup> Document clustering is a hierarchical clustering method. The algorithm starts by splitting all of the documents in a corpus into two groups such that the two groups differ as much as possible in their usage of words. Then, the same algorithm is applied to each of these groups separately to split the entire collection into four subgroups. Applying again yields eight subgroups, then sixteen, and so forth. We applied this algorithm five times to yield a set of 32 clusters. Due to the iterative method, the clusters are related to one another. Documents in cluster 1 and cluster 2, for example, were split only in the final round of the algorithm.</p> <p>All of the code to produce the text analysis models is made available under the open-source GNU Public License (GPL-2).<sup className="tooltip"><span>"FWP Life History Project in the American South: Text Analysis Code," <a target="_blank" rel="noopener noreferrer" href="https://github.com/statsmaths/fwp-life-histories-analysis">[link]</a>. Accessed February 28 2021.</span><div>[24]</div></sup> The code works directly off of the data described in the previous section. It also includes the code to create the JSON files that serve as the backend for the digital project.</p> </Collapsible> <Collapsible trigger="Digital Platform" overflowWhenOpen="visible"> <p>The project is a part of a growing community of scholars, publishers, and foundations working together to expand forms of academic scholarship, including what counts as evidence, ways of knowing, and communicating knowledge. Along with archival evidence, the creation, analysis, and communication of data sit at the core of this project. The digital platform offered an opportunity to make visible <em>what</em> kinds of data we created, <em>how</em> we analyzed the data, and <em>why</em> through visualizations and text. It also provided a space to communicate scholarship through visual ways of knowing – graphs, maps, and interactive visualizations. Additionally, the interactive visualizations encourage visitors to explore the archive alongside us, build off our scholarship, and pose their questions. As Cox and Tilton have written, developing open access and interactive digital public projects can expand our argumentative strategies and reorient the reader/viewer as not just a person to be persuaded but as a participant engaged in humanistic inquiry and communication.<sup className="tooltip"><span>Jordana Cox &amp; Lauren Tilton (2019) The digital public humanities: giving new arguments and new ways to argue, Review of Communication, 19:2, 127-146, DOI: 10.1080/15358593.2019.1598569</span><div>[25]</div></sup></p> <p>The platform is designed to pair text and interactive visualizations to convey the project’s arguments and scope. The project is structured in layers. As a chapter, each layer offers insights into the social, political, and cultural work of the SLHP. In the same way that audiences have learned how to read a text to interpret an argument, audiences also have tools to interpret visualizations; there is a system of symbols and signifiers that people have learned to "read" visualizations that they employ daily. This project uses visualizations such as interactive mapping as a form of argumentation and then puts them in conversation with textual argumentation. As a result, this project is not strictly a textual book on a digital platform as we harness layers and the interpretive power of interactive visualizations to convey a set of arguments through an interactive platform made possible by the affordances of digital technologies.</p> <p>The structure of the project builds off the spatial and visual turn in D.H. As scholars such as Tara McPherson, Jentery Sayers, and Lev Manovich have argued, D.H. remains a text-heavy field.<sup className="tooltip"><span>Manovich, Lev. <em>Cultural</em> Analytics, Cambridge: M.I.T. Press, 2021. McPherson, Tara. "Introduction: Media Studies and the Digital Humanities." <em>Cinema Journal</em> 48, no. 2 (2009): 119-23. Accessed March 1, 2021. <a target="_blank" rel="noopener noreferrer" href="http://www.jstor.org/stable/20484452">[link]</a>; Jentery Sayers, ed. <em>The Routledge Companion to Media Studies and Digital Humanities</em>. Routledge, 2018.</span><div>[26]</div></sup> The call to use the affordances of computational methods as visual ways of knowing, such as graphs and interactive visualizations, is amplifying. Led by scholars such as Stanford’s Richard White and over two decades of critical cartography, scholars have used visualizations such as maps to convey scholarly knowledge and arguments. Our project brings together these turns through the digital platform.</p> <p>The digital platform is written using a number of open-source technologies. The main functional elements of the site are written using the popular JavaScript framework ReactJS. Additional JavaScript packages were used within ReactJS to add specific functionality, such as the use of a router to create meaningful URLs and React-Dropdown to create interactive menus. Modern web standards are used to provide responsive, cross-platform compatible code using documented CSS3 and HTML5. The source code is available on GitHub.<sup className="tooltip"><span><a target="_blank" rel="noopener noreferrer" href="https://github.com/statsmaths/writing-their-voices">[link]</a></span><div>[27]</div></sup> Any original code produced for the project is under a GPL-2 license; some derivative components are released under an alternative open-source license as required by their respective authors.</p> <p>The mapping component of the website uses the JavaScript library Leaflet within ReactJS. Each of the maps was manually georeferenced using QGIS and projected into the Albers conic projection.<sup className="tooltip"><span>Gray, J. (2008). "Getting started with quantum G.I.S." <em>Linux Journal</em>.</span><div>[28]</div></sup> This projection preserves areas and more accurately represents distances between points when compared to a Mercator projection.<sup className="tooltip"><span>Kimerling, J. A., Overton, S. W., &amp; White, D. (1995). Statistical comparison of map projection distortions within irregular areas. Cartography and Geographic Information Systems, 22(3), 205-221.</span><div>[29]</div></sup> Map tiles were created using the Geospatial Data Abstraction Library (GDAL).<sup className="tooltip"><span>Warmerdam, F. (2008). The geospatial data abstraction library. In <em>Open-source approaches in spatial data handling</em> (pp. 87-104). Springer, Berlin, Heidelberg.</span><div>[30]</div></sup> The map tiles are served locally by the project; this is slightly less efficient than a purpose-built G.I.S. server but is more stable and reliable for long-term preservation and access to the digital project.</p> <p>The visualization of the topic models is written as a custom JavaScript code. It was adapted from a similar visualization produced for the Signs@40 project, made available under an open-source license.<sup className="tooltip"><span>Andrew Goldstone, Susana Galán, C. Laura Lovin, Andrew Mazzaschi, and Lindsey Whitmore. "An Interactive Topic Model of Signs", <a target="_blank" rel="noopener noreferrer" href="https://github.com/signs40th/topic-model">[link]</a>.</span><div>[31]</div></sup> All of the data for the visualizations are stored as JSON files to simplify deployment and increase the stability of our application. This design choice removes the need for a backend database, making it relatively easy to run the website from alternative sources. Keeping the backend of the website minimal also facilitates long-term access to the project by minimizing the ways that the site could become obsolete. Code to create the topic model files are included in the repository containing the code for producing the topic models.</p> </Collapsible> </div>
    )
}

export {Layer7};
